<template>
  <main class="subpage">
    <!-- 컨텐츠 영역 -->
    <div class="container">
      <!-- 왼쪽메뉴 -->
      <aside id="lnb">
        <ul>
          <li class="on"><a href="./location.html">location</a></li>
        </ul>
      </aside>
      <!-- 컨텐츠본문 -->
      <div class="content">
        <h2 class="sub_title"><span>Location</span></h2>
        <div class="inner kakao_map">
          <div id="map" style="width:1000px;height:350px; border: 1px solid red" ></div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  name: 'location-component',
  data () {
    return {
      msg: 'this is location'
    }
  }
}
</script>
<!--지도관련-->
<script type="text/javascript" src="//dapi.kakao.com/v2/maps/sdk.js?appkey=6723f746ec4d68f70212875e96acc483"></script>
<script>
  var mapContainer = document.getElementById('map'), // 지도를 표시할 div
    mapOption = {
      center: new kakao.maps.LatLng(35.868495, 128.594005), // 지도의 중심좌표
      level: 3 // 지도의 확대 레벨
    };

  var map = new kakao.maps.Map(mapContainer, mapOption); // 지도를 생성합니다

  // 마커가 표시될 위치입니다
  var markerPosition = new kakao.maps.LatLng(35.868495, 128.594005);

  // 마커를 생성합니다
  var marker = new kakao.maps.Marker({
    position: markerPosition
  });

  // 마커가 지도 위에 표시되도록 설정합니다
  marker.setMap(map);

  // 아래 코드는 지도 위의 마커를 제거하는 코드입니다
  // marker.setMap(null);
</script>



